import './App.css';
import {useState} from 'react';
import {useEffect} from 'react';
import CurrencyInput from './CurrencyInput';
import axios from 'axios';

function App() {


  const [amount1, setAmount1] = useState(1);
  const [amount2, setAmount2] = useState(1);
  const [currency1, setCurrency1] = useState('USD');
  const [currency2, setCurrency2] = useState('BDT');
  const [rates, setRates] = useState([]);

useEffect(() => {
  axios.get('https://api.exchangerate.host/latest').then(response =>{
  setRates(response.data.rates)

  })
},[]);

useEffect(() => {
  if(!!rates) {
    handleAmount1Change(1)
  }


// eslint-disable-next-line react-hooks/exhaustive-deps
},[rates]);

function formatNumber(number){
  return number.toFixed(4)
}
function handleAmount1Change(amount1){
  setAmount2(formatNumber(amount1 * rates[currency2] / rates[currency1]))
  setAmount1(amount1)
}
function handleCurrency1Change(currency1){
setAmount2(amount1 * rates[currency2] / rates[currency1])
  setCurrency1(currency1)
}
function handleAmount2Change(amount2){
  setAmount1(amount2 * rates[currency1] / rates[currency2])
  setAmount2(amount2)
}
function handleCurrency2Change(currency2){
setAmount1(amount2 * rates[currency1] / rates[currency1])
  setCurrency2(currency2)
}
function mobileNavClick() {
document.querySelector('body').classList.toggle('navShown')
}

  return (
    <>
    <div className="currency-wrapper">
    <nav className="nav-wrap">
    <div className="common-wrap clear flex flex-vertical-align">
      <div className="logo">
        <a href="https://webtricker.com/" target="_blank">
          <img src="img/webtricker-logo.svg" alt=""/>
        </a>
        <div className="mobi-nav" onClick={mobileNavClick}>
          <div></div>
        </div>
      </div>
      <ul>
        <li><a href="https://webtricker.com/" target="_blank">Home</a></li>
        <li><a href="https://webtricker.com/about-us/" target="_blank">About</a></li>
        <li><a href="https://webtricker.com/contact/" target="_blank">Contact Us</a></li>
      </ul>
    </div>
    
    </nav>
    <div className="main-content-wrap">
    <div className="common-wrap">
        <div className="intro-heading">
          <h2>Currency Converter</h2>
        </div>
        <div className="App flex flex-horizontal-align">
          <CurrencyInput onAmountChange={handleAmount1Change} onCurrencyChange={handleCurrency1Change} currencies={Object.keys(rates)} amount={amount1} currency={currency1}/>
          <CurrencyInput onAmountChange={handleAmount2Change} onCurrencyChange={handleCurrency2Change} currencies={Object.keys(rates)} amount={amount2} currency={currency2}/>
        </div>
        <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</p>
      </div>
    </div>
     <div className="bottom-nav flex flex-horizontal-align">
       <ul>
         <li>Created by <a href="https://webtricker.com/" target="_blank">Webtricker</a></li>
         <li>Made with React Js</li>
       </ul>
     </div>
    </div>
    </>
    
  );
}

export default App;
